'use strict'

import SE from 'se'
import Promise from 'bluebird'
import request from 'superagent-bluebird-promise'
import prefixify from 'superagent-prefix'

import { StackExchangeError, AUTHORIZATION_REQUIRED } from './stack-exchange-error'
import authenticationStore from '../stores/authentication'

import {
  SE_CLIENT_ID,
  SE_KEY,
  SE_CHANNEL_URL,
  SE_API_PREFIX,
  SE_SITE,
  SE_FILTER_QUESTIONS,
  SE_FILTER_QUESTION_DETAILS,
  SE_FILTER_USER_PROFILE,
  SE_FILTER_USER_BADGES
}
from '../config'

const prefix = prefixify(SE_API_PREFIX)

const init = new Promise(resolve => {
  SE.init({
    clientId: SE_CLIENT_ID,
    key: SE_KEY,
    channelUrl: SE_CHANNEL_URL,
    complete: resolve
  })
})

function apiRequest (resource) {
  let req = request.get(resource)
    .use(prefix)
    .set('Accept', 'application/json')
    .query({ site: SE_SITE })

  if (isAuthenticated()) {
    req.query({
      key: SE_KEY,
      access_token: authenticationStore.getState().get('accessToken')
    })
  }

  return req
}

function isAuthenticated () {
  return authenticationStore.getState().get('isAuthenticated')
}

export function authenticate () {
  return init.then(() => {
    return new Promise((resolve, reject) => {
      SE.authenticate({
        networkUsers: true,
        success: resolve,
        error: reject
      })
    })
  })
}

function getQuestionsParams (options) {
  return {
    sort: options.sort,
    filter: options.withDetails ? SE_FILTER_QUESTION_DETAILS : SE_FILTER_QUESTIONS
  }
}

export function fetchQuestions (options) {
  return apiRequest('/questions')
    .query(getQuestionsParams(options))
    .then(res => res.body)
}

export function searchQuestions (options) {
  return apiRequest('/search/advanced')
    .query(getQuestionsParams(options))
    .query({ q: options.query })
    .then(res => res.body)
}

export function fetchQuestionById (id, options) {
  return apiRequest('/questions/' + encodeURIComponent(id))
    .query(getQuestionsParams(options))
    .then(res => res.body)
}

export function fetchTags () {
  return apiRequest('/tags')
    .query({ pagesize: 99 })
    .then(res => res.body)
}

export function fetchTagWiki (id) {
  return apiRequest('/tags/' + encodeURIComponent(id) + '/wiki')
    .then(res => res.body)
}

export function fetchUserProfile () {
  if (!isAuthenticated()) {
    return Promise.reject(new StackExchangeError(AUTHORIZATION_REQUIRED))
  }

  let reqUser = apiRequest('/me')
    .query({ filter: SE_FILTER_USER_PROFILE })
    .then(res => res.body)

  let reqBadges = apiRequest('/me/badges')
    .query({ filter: SE_FILTER_USER_BADGES })
    .then(res => res.body)

  return Promise.join(reqUser, reqBadges)
}



