'use strict'

import Reflux from 'reflux'
import { default as Immutable, Map } from 'immutable'

import base from './base'
import actions from '../actions/users'

const store = Reflux.createStore({
  listenables: actions,

  mixins: [
    base
  ],

  init () {
    this.isLoading = false
    this.users = null
    this.badges = null
  },

  fetch () {
    this.isLoading = true
    this.refreshState()
  },

  fetchCompleted (users, badges) {
    this.isLoading = false
    this.users = Immutable.fromJS(users)
    this.badges = Immutable.fromJS(badges)
    this.refreshState()
  },

  fetchFailed () {
    // TODO
    this.isLoading = false
  },

  getState () {
    return new Map({
      isLoading: this.isLoading,
      users: this.users,
      badges: this.badges
    })
  }
})

export default store



