'use strict'

import React from 'react'
import Reflux from 'reflux'

import usersActions from '../actions/users'
import usersStore from '../stores/users'

const UserProfile = React.createClass({
  displayName: 'UserProfile',

  mixins: [
    Reflux.connect(usersStore, 'users')
  ],

  componentWillMount () {
    usersActions.fetch()
  },

  render () {
    let users = this.state.users.get('users')
    let item = users && users.getIn(['items', 0])
    let displayName = item && item.get('display_name')
    let aboutMe = item && item.get('about_me')

    let badges = this.state.users.get('badges')
    let badgeItems = badges && badges.get('items')
    let numBadges = badgeItems ? badgeItems.size : 0

    return (
      <div className='user-profile'>
        <h1 className='page-title'>{displayName}</h1>

        <div className='about-me'>
          {aboutMe}
        </div>

        <div className='badges'>
          <h2>{numBadges} Badges</h2>
          <ul className='badges-list'>
            {!badgeItems ? null : badgeItems.map(b => {
              let badgeId = b.get('badge_id')
              let badgeName = b.get('badge_name')

              return (
                <li key={badgeId}>
                  {badgeName}
                </li>
              )
            })}
          </ul>
        </div>
      </div>
    )
  }
})

export default UserProfile



